@use 'src/styles/abstracts' as *;

.BookmarkCard__container {
  position: relative;
  display: flex;
  flex-direction: column;
  border: 0.0625rem solid $cuddle-50;
  border-radius: 0.5rem;
  padding: 1.5rem;
  margin-bottom: 1rem;
}

.BookmarkCard__delete {
  margin-left: 0.25rem;
}

.BookmarkCard__top {
  flex: 1 1;

  p {
    margin-top: 0.25rem;
    font-weight: normal;
    font-size: $text-sm;
  }
}

.BookmarkCard__title__section {
  display: flex;
  justify-content: space-between;

  a {
    text-decoration: none;
  }
}

.BookmarkCard__title {
  font-weight: 600;
  font-size: 18px;
}

.BookmarkCard__bottom {
  margin-top: 0.75rem;

  strong {
    color: #243969;
    margin-right: 0.5rem;
  }
}

.BookmarkCard__selected__metrics {
  margin-top: 1rem;
  width: 100%;
  white-space: nowrap;
  overflow: auto;

  .TagLabel {
    margin-right: 0.5rem;
  }
}

.BookmarkCard__run__expression {
  margin-right: 2.8125rem;
}
